
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import axios from "axios";
import api from "../api";
import util from "../util";

export default {
components: {
PopupPicker,
Group,
XButton,
Cell,
Popup,
Checklist,
PopupHeader,
Alert
},
data() {
return {
school: [""],
school_title: "",
school_list: [["",""]], //
frequency: [""],
frequency_title: "",
frequency_list: [["", "", ""]],
type: [],
type_title: "",
type_list: [
"",
"",
"",
"",
"",
"",
"",
""
],
show: false,
if_required: true,
position: "left",
is_link: true,
max_num: 3,
alert_show:false
};
},
computed: {
type_computed: function() {
return this.type.join(",");
}
},

methods: {
get_user_init_setting() {
let _this = this;
axios
.get(api.get_user_init_setting)
.then(function(response) {
console.log(response.data);
if (response.data.data.school == null) {
_this.school = [""]

}else{
_this.school = [response.data.data.school];
}


if(!response.data.data.frequency){
_this.frequency == [""]
}
if (response.data.data.frequency == 1) {
console.log("_this.frequency[0]" + _this.frequency[0]);
console.log("");
_this.frequency = [""];
}
if (response.data.data.frequency == 3) {
console.log("_this.frequency[0]" + _this.frequency[0]);
console.log("");
_this.frequency = [""];
}
if (response.data.data.frequency == 7) {
console.log("_this.frequency[0]" + _this.frequency[0]);
console.log("");
_this.frequency[0] = [""];
}
_this.type = response.data.data.taglist;
util.wx_common_share();
})
.catch(function(error) {
console.log("");
console.log(error);
});
console.log("axiosthis.frequency");
console.log(this.frequency);
},
set_user_setting() {
let _this = this;
let temp_taglist = this.type;
let temp_frequency;
if (this.frequency[0] == "") {
temp_frequency = 1;
}
if (this.frequency[0] == "") {
temp_frequency = 3;
}
if (this.frequency[0] == "") {
temp_frequency = 7;
}
if(this.type == null){
temp_taglist = [];
}
console.log("type");
console.log(this.type);
axios
.post(api.set_user_setting, {
school: _this.school[0],
frequency: temp_frequency,
taglist: temp_taglist
})
.then(function(response) {
console.log("");
_this.get_user_init_setting();
console.log(response);
})
.catch(function(error) {
console.log(error);
});
this.alert_show = true;
setTimeout(() => {
this.alert_show = false;
}, 6000)
},


change_picker_state() {
console.log(this.type);
this.show = true;
}

},
mounted: function() {
util.to_wx_config();

let that = this
util.getCode("setting",that.get_user_init_setting());

},
created() {



}
};



